.modal-window {
    position: fixed;

    left: 0;
    right: 0;
    bottom: 0;
    top: 0;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 16px;

    background-color: rgba(0, 0, 0, 0.488);
}

.modal-window > img {
    object-fit: contain;
}

.buttons-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 8px;
}