.loading {
    border-radius: 16px;
    background-color: rgb(244, 244, 244);

    width: 100%;
    height: 128px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.loading > svg {
    animation: spinner 8.0s 0.0s cubic-bezier(.38,-0.12,.6,1.18) infinite;
}

@keyframes spinner {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(3600deg);
    }
}