.block {
    display: flex;
    flex-direction: column;

    row-gap: 16px;
}

.collection {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
}

img, .fakeImg {
    width: 100%;
    aspect-ratio: 1 / 0.7;
    object-fit: cover;
    border-radius: 16px;
    transition: all 0.2s ease-out 0s;
}

.fakeImg {
    background-color: rgb(209, 209, 209);
}