.album {
    display: flex;
    flex-direction: column;

    row-gap: 8px;

    cursor: pointer;
}

body {
    padding: 32px 64px;
}

.album, .photo {
    width: calc((100% - 32px) / 2);
}

@media (min-width: 1000px)  {
    body {
        padding: 64px 256px;
    }
    
    .album, .photo {
        width: calc((100% - 64px) / 3);
    }
}

.album:hover .album-title {
    text-decoration: underline;
}

.album:hover img, .album:hover .fakeImg, .photo:hover {
    opacity: 0.8;
}

.album-title {
    font-size: medium;
    transition: all 0.2s ease-out 0s;
}