@font-face {
	font-family: 'Gotham';
  font-weight: bold;
	src: url(./fonts/gotham_bold.otf); 
}

@font-face {
	font-family: 'Gotham';
  font-weight: lighter;
	src: url(./fonts/gotham_light.otf); 
}

body {
  margin: 0;
  padding: 64px 256px;
}

hr {
  width: 100%;
  border: none;
  background-color: black;
  height: 1px;
}

span {
  font-family: "Gotham";
  white-space: nowrap;
}

a {
  all: unset;
}