.button {
    outline: none;
    border: none;
    border-radius: 50%;
    transition: all 0.2s ease-out 0s;
    cursor: pointer;
    font-family: "Gotham";
    font-weight: bold;
    font-size: 30px;

    height: 40px;
    width: 40px;
    display: flex;
    flex-grow: 0;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    background-color: rgb(244, 244, 244);
}

.button:disabled {
    cursor: auto;
    background-color: rgb(189, 189, 189);
}

.button:hover:not(:disabled) {
    opacity: 0.7;
}

a > svg {
    margin-left: -2px;
    overflow: visible;
}